import React, { useState } from "react"

import { IRowsTableEvent } from "@types"
import {
  BasicDateRangePicker,
  Cover,
  DataTable,
  TableEditingButtons,
} from "@components"
import { exampleRowsTableEvent, columnsEventTable } from "./_constants"

/**
 * Фрагмент с таблицей журнала по датчикам
 *
 * @returns {React.FC} - FC
 */
export const EventTable: React.FC = () => {
  const [selectionRow, setSelectionRow] = useState<IRowsTableEvent>([])

  return (
    <Cover
      title="Журнал событий по датчику"
      autoHeight="autoHeight"
      footerChildren={
        <TableEditingButtons
          outputField="type"
          selectionRows={selectionRow}
          addingButtonHide
          editButtonHide
        />
      }
    >
      <BasicDateRangePicker />
      <DataTable
        rows={exampleRowsTableEvent}
        columns={columnsEventTable}
        height={420}
        setSelectionRows={setSelectionRow}
      />
    </Cover>
  )
}
