import { Box } from "@mui/material"
import React, { Fragment, ReactNode } from "react"

import { SPaper, SHeader, STypography, SFooter } from "./_styles"

interface ICover {
  title: string
  children: ReactNode
  headerChildren?: ReactNode
  footerChildren?: ReactNode
  showBorderTitle?: boolean
  autoHeight?: "autoHeight"
  indent?: boolean
}

/**
 * Обертка компонентов страниц
 *
 * @param {string} title - Заголовок
 * @param {ReactNode} children - Дочерний элемент
 * @param {ReactNode} headerChildren - Дочерний элемент для хедера
 * @param {boolean} isBorderedTitle - Для стилизации состояния: с бордером/без бордера
 * @param {ReactNode} footerChildren - Дочерний элемент для футера
 * @param {"autoHeight"} autoHeight - Для растягивания обертки на всю высоту экрана
 * @param {boolean} indent - Добавляет внутренние отступы
 * @param {object} otherProps - Любые другие значения
 * @returns {React.FC<ICover>} - FC
 */
export const Cover: React.FC<ICover> = ({
  title,
  children,
  headerChildren = null,
  footerChildren = null,
  showBorderTitle = false,
  autoHeight,
  indent = false,
  ...otherProps
}) => (
  <Fragment>
    <SPaper variant={autoHeight}>
      <SHeader
        sx={{
          borderBottomColor: showBorderTitle
            ? "transparent"
            : (theme): string => theme.palette.primary.main,
        }}
      >
        <STypography variant="h6">{title}</STypography>
        {headerChildren}
      </SHeader>
      <Box sx={indent ? { padding: "20px 20px 40px" } : null} {...otherProps}>
        {children}
      </Box>
    </SPaper>
    {footerChildren && <SFooter>{footerChildren}</SFooter>}
  </Fragment>
)
