import { Stack } from "@mui/material"
import React from "react"

import { BaseTextField, Cover } from "@components"
import { STextFieldName, STopStack, SDateRangePicker } from "./_styles"

/**
 * Фрагмент "Описание режима", для страницы "Добавление нового режима"
 *
 * @returns {React.FC} - FC
 */
export const ModeDescription: React.FC = () => (
  <Cover title="Описание режима" autoHeight="autoHeight" indent={true}>
    <STopStack direction="row" spacing={8}>
      <STextFieldName title="Название" label="Введите название" id="name" />
      <SDateRangePicker startText="С" endText="До" size="medium" />
    </STopStack>
    <Stack direction="row" spacing={8}>
      <BaseTextField
        title="Оборудование"
        label="Выберите оборудование"
        id="select-equipment"
      />
      <BaseTextField
        title="Описание"
        label="Введите описание"
        id="description"
      />
    </Stack>
  </Cover>
)
