import { styled, Typography, Button, Stack } from "@mui/material"

import { Select } from "@atoms"

const SStack = styled(Stack)({
  maxWidth: "860px",
  padding: "20px 20px 25px",
})

const STypography = styled(Typography)(({ theme }) => ({
  "maxWidth": "360px",
  "width": "100%",
  "marginRight": "20px",

  "& span": {
    marginTop: "8px",
    fontSize: "12px",
    lineHeight: "150%",
    letterSpacing: "0.15px",
    color: theme.palette.grey[600],
  },
}))

const SButton = styled(Button)({
  width: "137px",
  height: "30px",
})

const SSelect = styled(Select)({
  "& p": {
    maxWidth: "360px",
    width: "100%",
  },

  "& .MuiTextField-root": {
    width: "137px",
  },
})

export { SStack, STypography, SButton, SSelect }
