import { Button, Stack } from "@mui/material"
import SaveAltIcon from "@mui/icons-material/SaveAlt"
import DeleteForeverIcon from "@mui/icons-material/DeleteForever"
import React, { useState, Fragment, ChangeEvent } from "react"

import { BaseTextField } from "@atoms"
import { SImageFrame, SInput, SHelperText } from "./_styles"

/**
 * Форма загрузки изображений
 *
 * @param {object} otherProps - Любые другие значения
 * @returns {React.FC} - FC
 */
export const ImageUploadForm: React.FC = ({ ...otherProps }) => {
  const [image, setImage] = useState<{
    patch: string | ArrayBuffer | null
    name: string | null
  }>({
    patch: null,
    name: null,
  })

  function handleUploadClick(event: ChangeEvent<HTMLInputElement>): void {
    const fileReader = new FileReader()
    const currentFile = event.target.files

    if (currentFile !== null) {
      fileReader.readAsDataURL(currentFile[0])

      fileReader.onloadend = function (): void {
        setImage({
          patch: [fileReader.result][0],
          name: "",
        })
      }
    }
  }

  return (
    <Fragment>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        spacing={2}
      >
        <Stack position="relative">
          <BaseTextField
            title="Изображение"
            label="Выберите изображение"
            id="image-upload-form"
            disabled={true}
          />
          <SHelperText>Формат svg, png, jpg</SHelperText>
        </Stack>
        <label htmlFor="button-file">
          <SInput
            accept="image/*"
            id="button-file"
            type="file"
            onChange={handleUploadClick}
          />
          <Button
            variant="outlined"
            aria-label="upload picture"
            component="span"
            startIcon={<SaveAltIcon />}
          >
            Загрузить
          </Button>
        </label>
        <Button
          variant="outlined"
          startIcon={<DeleteForeverIcon />}
          disabled={!image.patch}
          onClick={(): void => {
            setImage({
              patch: null,
              name: "",
            })
          }}
        >
          Удалить
        </Button>
      </Stack>
      <SImageFrame {...otherProps}>
        {image.patch && (
          <img
            src={image.patch.toString()}
            alt="Оборудование"
            width="100%"
            height="100%"
          />
        )}
      </SImageFrame>
    </Fragment>
  )
}
