import { Stack } from "@mui/material"
import DateRangeIcon from "@mui/icons-material/DateRange"
import React from "react"
import { Link } from "react-router-dom"

import logo from "@assets/images/logo.svg"
import { useDateTimeGenerator } from "@hooks"
import { SAppBar, SToolbar, SIconButton, STypography } from "./_styles"

/**
 * Фрагмент верхней панели с датой, временем и кнопкой логина
 *
 * @returns {React.FC} - FC
 */
export const HeaderBar: React.FC = () => {
  const [date, time] = useDateTimeGenerator()

  return (
    <SAppBar position="static">
      <SToolbar>
        <Link to="/">
          <img src={logo} alt="Echo" />
        </Link>
        <Stack spacing={4} direction="row" alignItems="center">
          <SIconButton>
            <DateRangeIcon />
          </SIconButton>
          <STypography variant="h5">
            {date}
            <span>/</span>
            {time}
          </STypography>
        </Stack>
      </SToolbar>
    </SAppBar>
  )
}
