import React, { useState } from "react"

import { IRowsSensorRecordingTable } from "@types"
import {
  BasicDateRangePicker,
  Cover,
  DataTable,
  TableEditingButtons,
} from "@components"
import { columnsRecordsTable, exampleRowsTableRecords } from "./_constants"

interface ISensorRecordingTable {
  rows?: IRowsSensorRecordingTable
  height?: number
}

/**
 * Таблица "Записи датчиков"
 *
 * @param {IRowsSensorRecordingTable} rows - Содержимое строк таблицы
 * @param {number} height - Задает высоту таблицы
 * @returns {React.FC<ISensorRecordingTable>} - FC
 */
export const SensorRecordingTable: React.FC<ISensorRecordingTable> = ({
  rows = exampleRowsTableRecords,
  height,
}) => {
  const [selectionRows, setSelectionRows] = useState<IRowsSensorRecordingTable>(
    []
  )

  return (
    <Cover
      title="Записи датчиков"
      autoHeight="autoHeight"
      footerChildren={
        <TableEditingButtons
          selectionRows={selectionRows}
          addingButtonHide
          editButtonHide
        />
      }
    >
      <BasicDateRangePicker />
      <DataTable
        rows={rows}
        columns={columnsRecordsTable}
        setSelectionRows={setSelectionRows}
        height={height}
      />
    </Cover>
  )
}
