
import { Button, Stack, TextField, Typography } from "@mui/material"
import React from "react"

import ImageScheme from "@assets/images/scheme.svg"
import ImageEquipment from "@assets/images/equipment.svg"
import { Cover } from "@components"
import {
  SContainer,
  SCol,
  SHeaderCol,
  SBodyCol,
  STypography,
  SStack,
  SConnectionStatus,
} from "./_styles"

interface IModuleDetails {
  title: string
}

/**
 * Фрагмент с детальной инфомацией о модуле
 *
 * @returns {React.FC} - FC
 */
export const ModuleDetails: React.FC<IModuleDetails> = ({ title }) => {
  return (
    <Cover
      title={title}
      autoHeight="autoHeight"
      headerChildren={
        <Stack direction="row" spacing={2}>
          <Button variant="outlined">Редактировать</Button>
          <Button variant="outlined">Удалить</Button>
        </Stack>
      }
    >
      <SContainer>
        <SCol>
          <SConnectionStatus status={true} />
          <TextField
            id="outlined-basic"
            label="IP адрес (идентификатор)"
            variant="outlined"
            defaultValue="192.168.100.1.1"
            fullWidth
          />
          <STypography variant="body1">Статус записи</STypography>
          <TextField
            id="outlined-basic"
            label="Текущая запись"
            variant="outlined"
            defaultValue="Echo_24_09_12_21_19_40.wav"
            fullWidth
          />
          <SStack direction="row" spacing={2} alignItems="center">
            <TextField
              id="outlined-basic"
              label="Время"
              variant="outlined"
              defaultValue="00:12:34"
            />
            <Button variant="contained">остановить</Button>
          </SStack>
          <STypography variant="body1">Описание датчика</STypography>
          <TextField
            id="outlined-multiline-static"
            label="Описание"
            multiline
            rows={4}
            defaultValue="Тorem ipsum dolor sit amet, consectetur adipiscing elit. Ullamcorper orci luctus aliquet risus venenatis id enim eget."
            size="small"
            fullWidth
          />
        </SCol>
        <SCol>
          <SHeaderCol>
            <Typography variant="body1">
              Расположение на принципиальной схеме
            </Typography>
          </SHeaderCol>
          <SBodyCol>
            <img src={ImageScheme} alt="Принципиальная схема" width="630" />
          </SBodyCol>
        </SCol>
        <SCol>
          <SHeaderCol>
            <Typography variant="body1">КТН-1</Typography>
          </SHeaderCol>
          <SBodyCol>
            <img src={ImageEquipment} alt="КТН-1" width="630" />
          </SBodyCol>
        </SCol>
      </SContainer>
    </Cover>
  )
}
