import { ListItem, Toolbar, Typography } from "@mui/material"
import ExitToAppIcon from "@mui/icons-material/ExitToApp"
import React from "react"
import { NavLink, useLocation, useNavigate } from "react-router-dom"

import { itemNavigation } from "./_constants"
import { SDrawer, SList, SListItemText, SStack, SIconButton } from "./_styles"

/**
 * Фрагмент боковой панели навигации
 *
 * @returns {React.FC} - FC
 */
export const Sidebar: React.FC = () => {
  const { pathname } = useLocation()
  const navigate = useNavigate()

  function logout(): void {
    localStorage.removeItem("token")
    navigate("/login", { replace: true })
  }

  return (
    <SDrawer variant="permanent">
      <Toolbar />
      <SList>
        {itemNavigation.map(({ path, label, icon }) => (
          <NavLink
            to={path}
            key={label}
            className={({ isActive }): string =>
              isActive ? "nav_link_active" : ""
            }
          >
            <ListItem button sx={{ borderRight: "2px solid transparent" }}>
              <SListItemText>
                {icon(pathname.startsWith(path))}
                {label}
              </SListItemText>
            </ListItem>
          </NavLink>
        ))}
        <SStack>
          <SIconButton onClick={logout}>
            <ExitToAppIcon />
          </SIconButton>
          <Typography ml="12px">Выход</Typography>
        </SStack>
      </SList>
    </SDrawer>
  )
}
