import { Button, Stack, styled, TextField } from "@mui/material"
import React from "react"

/**
 * Форма поиска
 *
 * @param {object} otherData - Любые другие значения
 * @returns {React.FC} - FC
 */
export const SearchForm: React.FC = ({ ...otherData }) => (
  <Stack direction="row" alignItems="center" spacing={2} {...otherData}>
    <STextField type="search" size="small" placeholder="Поиск..." />
    <Button variant="contained">Найти</Button>
  </Stack>
)

const STextField = styled(TextField)({
  width: "350px",
})
