import React, { useState } from "react"

import { IRowsTableEquipments } from "@types"
import { Cover, DataTable, TableEditingButtons } from "@components"
import {
  exampleTableEquipments,
  headerEquipmentsTable,
  exampleEquipments,
} from "./_constants"
import { Filter } from "./fragments"

/**
 * Страница Оборудование
 *
 * @returns {React.FC} - FC
 */
const EquipmentsPage: React.FC = () => {
  const [selectionRows, setSelectionRows] = useState<IRowsTableEquipments>([])

  return (
    <Cover
      title="Журнал оборудования"
      footerChildren={
        <TableEditingButtons
          selectionRows={selectionRows}
          addingButtonPatch="/equipment/adding"
        />
      }
    >
      <Filter equipmentsList={exampleEquipments} />
      <DataTable
        rows={exampleTableEquipments}
        columns={headerEquipmentsTable}
        setSelectionRows={setSelectionRows}
      />
    </Cover>
  )
}

export default EquipmentsPage
