import { Toolbar } from "@mui/material"
import React from "react"
import { Navigate, Outlet, useLocation } from "react-router-dom"

import { HeaderBar, Sidebar } from "./fragments"
import { SWrapper, SContainer } from "./_styles"

/**
 * Обертка для всех страниц c хедером и навигацией
 *
 * @returns {React.FC} - FC
 */
export const PageWrapper: React.FC = () => {
  const { pathname } = useLocation()
  const localStorageRef = localStorage.getItem("token")
  const sessionStorageRef = sessionStorage.getItem("token")

  if (!localStorageRef && pathname !== "/login") {
    return <Navigate to="/login" replace />
  }

  return (
    <SWrapper>
      <HeaderBar />
      {pathname !== "/login" ? <Sidebar /> : null}
      <SContainer component="main">
        <Toolbar />
        <Outlet />
      </SContainer>
    </SWrapper>
  )
}
