import React, { ChangeEvent, useState } from "react"
import { Stack, styled } from "@mui/material"

import { ISelectMenuItemsList } from "@types"
import { BasicDateRangePicker, SearchForm, Select } from "@components"

interface IFilter {
  equipmentsList: ISelectMenuItemsList
}

/**
 * Фильтр для таблицы "Записи датчиков"
 *
 * @param {ISelectMenuItemsList} equipmentsList - Список оборудования, для селекта
 * @returns {React.FC<IFilter>} - FC
 */
export const Filter: React.FC<IFilter> = ({ equipmentsList }) => {
  const [currentEquipment, setCurrentEquipment] = useState<string>("Все")

  return (
    <Stack direction="row" m="10px 15px" justifyContent="space-between">
      <SearchForm />
      <SBasicDateRangePicker label="Период" />
      <Select
        label="Оборудование"
        initialState={currentEquipment}
        onChange={(event: ChangeEvent<HTMLInputElement>): void =>
          setCurrentEquipment(event.target.value)
        }
        menuItemsList={equipmentsList}
      />
    </Stack>
  )
}

const SBasicDateRangePicker = styled(BasicDateRangePicker)({
  padding: "0",
})
