import { createTheme } from "@mui/material"
import {
  DataGridComponentNameToClassKey,
  DataGridComponents,
  DataGridComponentsPropsList,
} from "@mui/x-data-grid/themeAugmentation"

interface ITheme {
  components?:
    | { [key: string]: any }
    | DataGridComponentsPropsList
    | DataGridComponents
    | DataGridComponentNameToClassKey
    | undefined
  palette?: { [key: string]: any }
}

// Это необходимо, для расширения получаемых вариантов стилизации компонента Paper
declare module "@mui/material/Paper" {
  interface PaperPropsVariantOverrides {
    autoHeight: true
    indent: true
  }
}

/*
 * Кастомизация темы Material UI и его компонентов
 */
export const theme: ITheme = createTheme({
  //  Components //
  components: {
    //  Стилизация DataGrid и ее элементов //
    MuiDataGrid: {
      styleOverrides: {
        root: {
          "border": "none",

          "a": {
            textDecoration: "none",
            color: "#fff",
          },

          "& .Mui-selected": {
            background: "#C50E54 !important",
          },

          "& .MuiDataGrid-checkboxInput": {
            color: "#FFF !important",
          },

          "& .MuiDataGrid-menuIconButton": {
            display: "none",
          },

          "& .MuiDataGrid-columnHeaderTitleContainer": {
            padding: 0,
          },

          "& .MuiDataGrid-columnSeparator": {
            display: "none",
          },

          "& .MuiDataGrid-columnHeaders *:focus-within, .MuiDataGrid-columnHeaders *:focus":
            {
              outline: "none !important",
            },

          "& .MuiDataGrid-cell:focus-within, & .MuiDataGrid-cell:focus": {
            outline: "none !important",
          },
        },
      },
    },

    MuiDrawer: {
      styleOverrides: {
        root: {
          ".MuiList-root": {
            height: "inherit",
          },
        },
      },
    },

    //  Стилизация контейнера Toolbar //
    MuiToolbar: {
      styleOverrides: {
        root: {
          minHeight: "50px !important",
        },
      },
    },

    // Tabs //
    MuiTab: {
      styleOverrides: {
        root: {
          "&.MuiTab-root": {
            maxWidth: "100%",
            boxShadow: "inset 0px -.5px 0px rgba(255, 255, 255, 0.63)",
          },
          "&.MuiTabs-flexContainer": {
            maxWidth: "100%",
            display: "flex",
            justifyContent: "space-between",
          },

          "&.Mui-selected": {
            color: "#FFF",
            background:
              "linear-gradient(90deg, rgba(136, 136, 136, 0.1) 0%, rgba(147, 147, 147, 0.1) 100.83%)",
          },
        },
      },
    },

    // Button //
    MuiButton: {
      styleOverrides: {
        root: {
          color: "#FFF",
        },
      },
    },

    // Alert //
    MuiAlert: {
      styleOverrides: {
        root: {
          backgroundColor: "transparent",
        },
      },
    },

    // Стилизация контейнера Paper //
    MuiPaper: {
      variants: [
        {
          props: { variant: "autoHeight" },
          style: {
            minHeight: "auto !important",
            marginBottom: "20px !important",
          },
        },
      ],
      styleOverrides: {
        root: {
          background: "#030303",
        },
      },
    },

    // Стилизация текстового поля //
    MuiTextField: {
      styleOverrides: {
        root: {
          fontStyle: "18px",
        },
      },
    },

    // MUI CSS Baseline //
    MuiCssBaseline: {
      styleOverrides: {
        // Scroll styles of selects and popups //
        ".css-35ia9b-MuiPaper-root-MuiMenu-paper-MuiPaper-root-MuiPopover-paper":
          {
            "background": "rgba(20, 20, 20, 0.8) !important",
            "backdropFilter": "blur(2px)",
            "overflow": "hidden",

            "& ul": {
              "background": "transparent",
              "boxShadow": "inset 1px 0px 0px #2F2F2F",
              "direction": "ltr",
              "maxHeight": "300px",
              "overflowY": "auto",
              "scrollbarColor": "#d4aa70",
              "scrollbarWidth": "thin",

              "&::-webkit-scrollbar": {
                width: "6px",
              },

              "&::-webkit-scrollbar-track": {
                borderRadius: "50px",
                background: "rgba(20, 20, 20, 1)",
                boxShadow: "inset 1px 0px 0px #2F2F2F",
                overflow: "hidden",
              },

              "&::-webkit-scrollbar-thumb": {
                background: "#898989",
                borderRadius: "100px",
                boxShadow: "inset 2px 2px 5px 0 rgba(#fff, 0.5)",
              },

              ".Mui-selected": {
                "backgroundColor": "#2D2D2D",
                "boxShadow": "inset 0px -1px 0px #FFFFFF",

                "&:hover, &:focus": {
                  backgroundColor: "#2D2D2D",
                },
              },
            },
          },
        // Global styles of links
        "a": {
          textDecoration: "none",
        },
        // Datepicker styles
        ".css-1xhj18k": {
          border: "2px solid rgba(255, 255, 255, 0.12)",
        },
      },
    },
  },

  //  Colors //
  palette: {
    primary: {
      main: "#DB0B5B",
      light: "#E62670",
      dark: "#C50E54",
      contrastText: "#FFF",
    },

    secondary: {
      main: "#EF0202",
      light: "#F92F2F",
      contrastText: "#FFF",
    },

    success: {
      main: "#4CAF50",
      light: "#3FBE5B",
      contrastText: "#FFF",
    },

    mode: "dark",

    text: {
      primary: "#FFF",
      secondary: "#6B6B6B",
      disabled: "#3F3F3F",
    },

    background: {
      default: "#030303",
    },
  },
})
