import {
  IconEquipping,
  IconModes,
  IconRecords,
  IconSensor,
  IconSettings,
  IconSystems,
} from "@atoms"

type IItemNavigation = {
  path: string
  label: string
  icon(isDark: boolean): JSX.Element
}[]

/**
 * Константы пунктов меню (боковая панель)
 */
export const itemNavigation: IItemNavigation = [
  {
    path: "/dashboard",
    label: "Системы",
    icon: (isDark) => (isDark ? <IconSystems isDark /> : <IconSystems />),
  },
  {
    path: "/sensors",
    label: "Датчики",
    icon: (isDark) => (isDark ? <IconSensor isDark /> : <IconSensor />),
  },
  {
    path: "/echo-records",
    label: "Записи Echo",
    icon: (isDark) => (isDark ? <IconRecords isDark /> : <IconRecords />),
  },
  {
    path: "/equipment",
    label: "Оборудование",
    icon: (isDark) => (isDark ? <IconEquipping isDark /> : <IconEquipping />),
  },
  {
    path: "/modes",
    label: "Режимы",
    icon: (isDark) => (isDark ? <IconModes isDark /> : <IconModes />),
  },
  {
    path: "/settings",
    label: "Настройки",
    icon: (isDark) => (isDark ? <IconSettings isDark /> : <IconSettings />),
  },
]
