import { Button, Stack, TextField, Typography } from "@mui/material"
import { LocalizationProvider, DatePicker } from "@mui/lab"
import AdapterDateFns from "@mui/lab/AdapterDateFns"
import React, { Fragment, useState } from "react"

import {
  BasicDateRangePicker,
  BasicModal,
  Cover,
  DataTable,
  SearchForm,
  TableEditingButtons,
} from "@components"
import { IRowsSensorParameters } from "@types"
import { columnsTableSensorParameters } from "./_constants"
import { SStack, SBaseTextField, SColumnForm } from "./_styles"

interface ISensorParameters {
  rowsSensorParameters: IRowsSensorParameters
}

/**
 * Фрагмент таблицы "Журнал значений измеряемого параметра датчика"
 *
 * @param {IRowsSensorParameters} rowsSensorParameters - содержимое строк таблицы
 * @returns {React.FC<ISensorParameters>} - FC
 */
export const SensorParameters: React.FC<ISensorParameters> = ({
  rowsSensorParameters,
}) => {
  const [selectionRows, setSelectionRows] = useState<IRowsSensorParameters>([])
  const [dateRecordingValue, setDateRecordingValue] = useState<Date | null>(
    null
  )
  const [isOpenModal, setIsOpenModal] = useState(false)

  return (
    <Fragment>
      <Cover
        title="Журнал значений измеряемого параметра датчика"
        autoHeight="autoHeight"
        headerChildren={
          <Button
            variant="outlined"
            onClick={(): void => setIsOpenModal(!isOpenModal)}
          >
            Добавить значение
          </Button>
        }
        footerChildren={
          <TableEditingButtons
            selectionRows={selectionRows}
            outputField="type"
            addingButtonHide
          />
        }
      >
        <SStack direction="row" spacing={2}>
          <SearchForm />
          <BasicDateRangePicker />
        </SStack>
        <DataTable
          rows={rowsSensorParameters}
          columns={columnsTableSensorParameters}
          setSelectionRows={setSelectionRows}
          height={420}
        />
      </Cover>

      <BasicModal
        setIsOpenModal={(): void => setIsOpenModal(!isOpenModal)}
        handleButtonSuccess={(): void => alert("Сохранено")}
        isOpenModal={isOpenModal}
        title="Добавить значение датчика"
      >
        <Stack direction="column" spacing={3}>
          <Typography children="Ручной ввод показания датчика" />
          <SColumnForm
            direction="row"
            spacing={3}
            alignItems="center"
            justifyContent="space-between"
          >
            <Typography children="Дата записи" />
            <LocalizationProvider dateAdapter={AdapterDateFns}>
              <DatePicker
                label="Выбор даты и времени"
                value={dateRecordingValue}
                onChange={(newValue) => setDateRecordingValue(newValue)}
                renderInput={(params) => <TextField {...params} />}
              />
            </LocalizationProvider>
          </SColumnForm>
          <SBaseTextField
            title="Значение"
            label="Текущее значение"
            id="current-value"
          />
          <SColumnForm
            direction="row"
            spacing={3}
            alignItems="center"
            justifyContent="space-between"
          >
            <Typography children="Комментарий" />
            <TextField
              id="comment"
              label="Введите примечание"
              multiline
              rows={2}
            />
          </SColumnForm>
        </Stack>
      </BasicModal>
    </Fragment>
  )
}
