import {
  DataGrid,
  GridColDef,
  GridRowModel,
  GridSelectionModel,
  ruRU,
} from "@mui/x-data-grid"
import React, { useState } from "react"

// Todo Egor: добавить типы
interface IDataTable {
  rows: GridRowModel[]
  columns: GridColDef[]
  setSelectionRows?: any
  checkboxSelection?: boolean
  height?: number | string
}

/**
 * Компонент DataGrid, для отрисовки таблиц
 *
 * @param {readonly} rows - Массив с данными
 * @param {GridColDef[]} columns - Заголовки ячеек
 * @param {boolean} checkboxSelection - Наличие чекбокосов у строк таблицытаблицы
 * @param {    } setSelectionRows - Событие, для получения выбраных строк в таблице
 * @param {number} height - Задает высоту таблицы
 * @returns {React.FC<IDataTable>} - FC
 */
export const DataTable: React.FC<IDataTable> = ({
  rows,
  columns,
  checkboxSelection = true,
  setSelectionRows,
  height,
}) => {
  const [pageSize, setPageSize] = useState<number>(20)

  function onSelectionRows(id: GridSelectionModel): void {
    const selectedIDs = new Set(id)
    setSelectionRows(rows.filter(({ id }) => selectedIDs.has(id)))
  }

  return (
    <div style={{ height: `${height}px` }}>
      <DataGrid
        rows={rows}
        columns={columns.map((column) => ({
          ...column,
          sortable: false,
        }))}
        autoHeight={!height}
        pageSize={pageSize}
        onPageSizeChange={(newPage): void => setPageSize(newPage)}
        rowsPerPageOptions={[20, 50, 100]}
        checkboxSelection={checkboxSelection}
        disableSelectionOnClick
        disableColumnFilter
        disableColumnSelector
        localeText={ruRU.components.MuiDataGrid.defaultProps.localeText}
        onSelectionModelChange={onSelectionRows}
      />
    </div>
  )
}
