import { Tab, Tabs } from "@mui/material"
import React, { Fragment, useState } from "react"

import {
  IRowsAsideSensorTable,
  IRowsSensorEchoTable,
  IRowsVirtualSensorTable,
  ISelectMenuItemsList,
} from "@types"
import { Cover, TableEditingButtons } from "@components"
import { AsideSensors, EchoSensors, VirtualSensors } from "./fragments"
import { tableTabsHeaders } from "./_constants"

interface IConnectedSensorsTable {
  echoSensorsEquipmentsList: ISelectMenuItemsList
  echoSensorsConnectionStatusList: ISelectMenuItemsList
  echoSensorsRecordsStatusList: ISelectMenuItemsList
  echoSensorsRows: IRowsSensorEchoTable
  virtualSensorsRows: IRowsVirtualSensorTable
  virtualSensorsEquipmentList: ISelectMenuItemsList
  virtualSensorsParameterList: ISelectMenuItemsList
  asideSensorsRows: IRowsAsideSensorTable
  asideSensorsParameterList: ISelectMenuItemsList
  autoHeight?: "autoHeight"
  height?: number
}

/**
 * Таблица "Журнал подключенных датчиков"
 *
 * @param {IRowsSensorEchoTable} echoSensorsRows -  содержимое строк таблицы, вкладки "Сенсоры ECHO"
 * @param {ISelectMenuItemsList} echoSensorsEquipmentsList - список оборудования для селекта фильтра вкладки "Сенсоры ECHO"
 * @param {ISelectMenuItemsList} echoSensorsConnectionStatusList - список статусов для селекта фильтра вкладки "Сенсоры ECHO"
 * @param {ISelectMenuItemsList} echoSensorsRecordsStatusList - список статусов для селекта фильтра вкладки "Сенсоры ECHO"
 *
 * @param {IRowsAsideSensorTable} asideSensorsRows - содержимое строк таблицы, вкладки "Внешние датчики"
 * @param {ISelectMenuItemsList} asideSensorsParameterList - список параметров для селекта фильтра вкладки "Внешние датчики"
 *
 * @param {IRowsVirtualSensorTable} virtualSensorsRows -  содержимое строк таблицы, вкладки "Виртуальные датчики"
 * @param {ISelectMenuItemsList} virtualSensorsParameterList - список параметров для селекта фильтра вкладки "Виртуальные датчики"
 * @param {ISelectMenuItemsList} virtualSensorsEquipmentList - список оборудования для селекта фильтра вкладки "Виртуальные датчики"
 *
 * @param {number} height - Задает высоту таблицы
 * @param {autoHeight} autoHeight - Для растягивания обертки на всю высоту экрана
 * @returns {React.FC<IConnectedSensorsTable>} - FC
 */
export const ConnectedSensorsTable: React.FC<IConnectedSensorsTable> = ({
  echoSensorsRows,
  echoSensorsEquipmentsList,
  echoSensorsConnectionStatusList,
  echoSensorsRecordsStatusList,
  asideSensorsRows,
  asideSensorsParameterList,
  virtualSensorsRows,
  virtualSensorsParameterList,
  virtualSensorsEquipmentList,
  autoHeight,
  height,
}) => {
  const [activeTab, setActiveTab] = useState<string>(tableTabsHeaders[0].value)
  const [selectionRows, setSelectionRows] = useState<
    IRowsSensorEchoTable | IRowsAsideSensorTable | IRowsVirtualSensorTable
  >([])

  return (
    <Cover
      autoHeight={autoHeight}
      title="Журнал подключенных датчиков"
      showBorderTitle={true}
      footerChildren={
        <Fragment>
          {activeTab === tableTabsHeaders[0].value && (
            <TableEditingButtons
              selectionRows={selectionRows}
              addingButtonPatch="/sensors/adding"
            />
          )}
          {activeTab === tableTabsHeaders[1].value && (
            <TableEditingButtons
              addingButtonHide={true}
              addingButtonDisabled={true}
              selectionRows={selectionRows}
              addingButtonPatch="/sensors/adding"
            />
          )}
          {activeTab === tableTabsHeaders[2].value && (
            <TableEditingButtons
              selectionRows={selectionRows}
              addingButtonPatch="/virtual-sensor/adding"
            />
          )}
        </Fragment>
      }
    >
      <Tabs
        value={activeTab}
        variant="fullWidth"
        onChange={(event, newTab: string): void => {
          setSelectionRows([])
          setActiveTab(newTab)
        }}
      >
        {tableTabsHeaders.map(({ value, label, disabled }) => (
          <Tab value={value} label={label} key={value} disabled={disabled} />
        ))}
      </Tabs>
      {activeTab === tableTabsHeaders[0].value && (
        <EchoSensors
          rows={echoSensorsRows}
          height={height}
          equipmentsList={echoSensorsEquipmentsList}
          connectionStatusList={echoSensorsConnectionStatusList}
          recordsStatusList={echoSensorsRecordsStatusList}
          setSelectionRows={setSelectionRows}
        />
      )}
      {activeTab === tableTabsHeaders[1].value && (
        <AsideSensors
          setSelectionRows={setSelectionRows}
          parameterList={asideSensorsParameterList}
          rows={asideSensorsRows}
          height={height}
        />
      )}
      {activeTab === tableTabsHeaders[2].value && (
        <VirtualSensors
          rows={virtualSensorsRows}
          height={height}
          parameterList={virtualSensorsParameterList}
          equipmentList={virtualSensorsEquipmentList}
          setSelectionRows={setSelectionRows}
        />
      )}
    </Cover>
  )
}
