import { AppBar, Toolbar, styled, IconButton, Typography } from "@mui/material"

const SToolbar = styled(Toolbar)({
  display: "flex",
  justifyContent: "space-between",
  padding: "0 20px 0 30px !important",
})

const SAppBar = styled(AppBar)(({ theme }) => ({
  zIndex: 1201,
  position: "fixed",
  backgroundColor: `${theme.palette.background.default}`,
  boxShadow: `inset 0px -1px 0px ${theme.palette.primary.main}`,
}))

const SIconButton = styled(IconButton)({
  width: "40px",
  height: "40px",
  borderRadius: "6px",
  backgroundColor: "#161616",
  cursor: "unset",
})

const STypography = styled(Typography)({
  "fontWeight": 700,
  "lineHeight": "28px",

  "& span": {
    margin: "0 12px",
    color: "#303030",
    fontWeight: "inherit",
    fontSize: "24px",
    lineHeight: "28px",
  },
})

export { SToolbar, SAppBar, SIconButton, STypography }
