import React from "react"

import { ConnectedSensorsTable } from "@components"
import {
  exampleSensorTable,
  exampleAsideSensorTable,
  exampleVirtualSensorTable,
  exampleEquipments,
  exampleRecordsStatus,
  exampleConnectionStatus,
  exampleParameter,
} from "./_constants"

/**
 * Страница датчиков
 *
 * @returns {React.FC} - FC
 */
const SensorsPage: React.FC = () => (
  <ConnectedSensorsTable
    echoSensorsRows={exampleSensorTable}
    echoSensorsConnectionStatusList={exampleConnectionStatus}
    echoSensorsRecordsStatusList={exampleRecordsStatus}
    echoSensorsEquipmentsList={exampleEquipments}
    asideSensorsRows={exampleAsideSensorTable}
    asideSensorsParameterList={exampleParameter}
    virtualSensorsRows={exampleVirtualSensorTable}
    virtualSensorsEquipmentList={exampleEquipments}
    virtualSensorsParameterList={exampleParameter}
  />
)

export default SensorsPage
