import { Button, Stack, TextField, Typography } from "@mui/material"
import React, { ChangeEvent, useState } from "react"

import ImageScheme from "@assets/images/scheme.svg"
import ImageEquipment from "@assets/images/equipment.svg"
import { Cover, Select } from "@components"
import { ISelectMenuItemsList } from "@types"
import { SContainer, SCol, SHeaderCol, SBodyCol } from "./_styles"

interface IModuleDetails {
  parameters: ISelectMenuItemsList
  title: string
}

/**
 * Фрагмент с детальной инфомацией о модуле
 *
 * @returns {React.FC} - FC
 */
export const ModuleDetails: React.FC<IModuleDetails> = ({
  title,
  parameters,
}) => {
  const [currentParameter, setCurrentParameter] = useState<string>("Все")

  return (
    <Cover
      title={title}
      autoHeight="autoHeight"
      headerChildren={
        <Stack direction="row" spacing={2}>
          <Button variant="outlined">Редактировать</Button>
          <Button variant="outlined">Удалить</Button>
        </Stack>
      }
    >
      <SContainer>
        <SCol>
          <TextField
            id="outlined-multiline-static"
            label="Описание"
            multiline
            rows={4}
            defaultValue="Тorem ipsum dolor sit amet, consectetur adipiscing elit. Ullamcorper orci luctus aliquet risus venenatis id enim eget. "
            size="small"
            fullWidth
          />
        </SCol>
        <SCol>
          <SHeaderCol>
            <Typography variant="body1">
              Расположение на принципиальной схеме
            </Typography>
          </SHeaderCol>
          <SBodyCol>
            <img src={ImageScheme} alt="Принципиальная схема" />
          </SBodyCol>
        </SCol>
        <SCol>
          <SHeaderCol>
            <Stack direction="row" alignItems="center" spacing={4}>
              <Typography variant="body1">Расположение датчиков</Typography>
              <Select
                label="Параметр"
                initialState={currentParameter}
                onChange={(event: ChangeEvent<HTMLInputElement>): void =>
                  setCurrentParameter(event.target.value)
                }
                menuItemsList={parameters}
              />
            </Stack>
          </SHeaderCol>
          <SBodyCol>
            <img src={ImageEquipment} alt="КТН-1" />
          </SBodyCol>
        </SCol>
      </SContainer>
    </Cover>
  )
}
