import { Stack, styled, TextField } from "@mui/material"

const SStack = styled(Stack)({
  flexDirection: "row",
  alignItems: "center",
})

const STextField = styled(TextField)(({ theme }) => ({
  "width": "220px",
  "marginLeft": "20px",

  "& .MuiInputBase-inputSizeSmall": {
    padding: "3.5px 14px",
  },

  "& .MuiOutlinedInput-root": {
    "position": "relative",
    "overflow": "hidden",

    "&::after": {
      content: `""`,
      position: "absolute",
      top: "1px",
      bottom: "1px",
      right: "38px",
      width: "1px",
      backgroundColor: "rgba(255, 255, 255, 0.23)",
    },

    "&:hover::after": {
      backgroundColor: theme.palette.common.white,
    },

    "&.Mui-focused::after": {
      width: "2px",
      backgroundColor: theme.palette.primary.dark,
    },
  },
}))

export { SStack, STextField }
