import { Link, Typography } from "@mui/material"
import NavigateNextIcon from "@mui/icons-material/NavigateNext"
import React from "react"
import { useNavigate } from "react-router-dom"

import { SBreadcrumbs } from "./_styles"

interface IBreadcrumbs {
  breadcrumbs: string[]
}

/**
 * Хлебные крошки
 * !В макетах на данный момент глубина только в одну страницу, если появится
 * компонент с глубиной больше 2 страниц, то переписать на отрисовку входящего
 * массива через цикл.
 *
 * @params {string[]} breadcrumbs - массив с названием страниц
 * @returns {React.FC} - FC
 */
export const Breadcrumbs: React.FC<IBreadcrumbs> = ({ breadcrumbs }) => {
  const navigate = useNavigate()

  return (
    <SBreadcrumbs
      separator={<NavigateNextIcon fontSize="small" color="primary" />}
      aria-label="breadcrumb"
    >
      <Link
        key="1"
        color="inherit"
        underline="hover"
        onClick={(): void => navigate(-1)}
        sx={{ cursor: "pointer" }}
      >
        {breadcrumbs[0]}
      </Link>
      <Typography key="3" color="primary.light">
        {breadcrumbs[1]}
      </Typography>
    </SBreadcrumbs>
  )
}
