import {
  FormControl,
  FormHelperText,
  IconButton,
  InputAdornment,
  InputLabel,
  OutlinedInput,
  Stack,
  styled,
} from "@mui/material"
import { Visibility, VisibilityOff } from "@mui/icons-material"
import React, { ChangeEvent, useState } from "react"

interface IPasswordField {
  password: string
  setPassword: (event: ChangeEvent<HTMLInputElement>) => void
  error: boolean
}

/**
 * Фрагмент поля ввода пароля
 *
 * @param {string} password - Значение поле ввода пароля
 * @param {function} setPassword - Функция устанавливающая новое значение
 * @param {boolean} error - Ошибка при неправильном заполнении полей
 * @returns {React.FC<IPasswordField>} - FC
 */
export const PasswordField: React.FC<IPasswordField> = ({
  error,
  password,
  setPassword,
}) => {
  const [showPassword, setShowPassword] = useState<boolean>(false)

  return (
    <SFormControl fullWidth variant="outlined">
      <InputLabel htmlFor="outlined-adornment-password" error={error}>
        Пароль
      </InputLabel>
      <OutlinedInput
        autoComplete="off"
        error={error}
        id="password"
        label="Пароль"
        type={showPassword ? "text" : "password"}
        value={password}
        onChange={setPassword}
        endAdornment={
          <InputAdornment position="end">
            <IconButton
              aria-label="toggle password visibility"
              onClick={(): void => setShowPassword(!showPassword)}
              edge="end"
            >
              {showPassword ? <VisibilityOff /> : <Visibility />}
            </IconButton>
          </InputAdornment>
        }
      />
      <Stack height="20px">
        {error && (
          <FormHelperText error={error}>
            Неверный логин или пароль
          </FormHelperText>
        )}
      </Stack>
    </SFormControl>
  )
}

const SFormControl = styled(FormControl)({
  margin: "20px 0 40px",
})
