import { Checkbox, TextField } from "@mui/material"
import React, { useState, ChangeEvent } from "react"
import { Navigate, useNavigate } from "react-router-dom"

import logo from "@assets/images/logo.svg"
import {
  SLogo,
  SBox,
  SFormGroup,
  SStack,
  SButton,
  SFormControlLabel,
} from "./_styles"
import { PasswordField } from "./fragments"

/**
 * Страница входа в систему Echo
 *
 * @returns {React.FC} - FC
 */
const LoginPage: React.FC = () => {
  const navigate = useNavigate()
  const [email, setEmail] = useState<string>("")
  const [password, setPassword] = useState<string>("")
  const [rememberMe, setRememberMe] = useState<boolean>(true)
  const [error, setError] = useState<boolean>(false)

  const LOGIN = "echo@echo.com"
  const PASSWORD = "echo"

  function eventLogin(): void {
    if (email === LOGIN && password === PASSWORD) {
      localStorage.setItem("token", "ExampleEchoToken")
      setError(false)

      navigate("/", { replace: true })
    } else {
      localStorage.removeItem("token")
      setError(true)
    }
  }

  if (localStorage.getItem("token")) {
    return <Navigate to="/" replace />
  }

  return (
    <SBox>
      <SFormGroup>
        <SLogo src={logo} alt="Echo" />
        <TextField
          error={error}
          id="email"
          label="Логин"
          variant="outlined"
          fullWidth
          autoComplete="off"
          value={email}
          onChange={(event: ChangeEvent<HTMLInputElement>): void =>
            setEmail(event.target.value)
          }
        />
        <PasswordField
          password={password}
          setPassword={(event: ChangeEvent<HTMLInputElement>): void =>
            setPassword(event.target.value)
          }
          error={error}
        />
        <SStack>
          <SButton
            variant="contained"
            onClick={eventLogin}
            children="Войти"
            size="large"
          />
          <SFormControlLabel
            label="Запомнить меня"
            control={
              <Checkbox
                checked={rememberMe}
                onChange={(): void => setRememberMe(!rememberMe)}
              />
            }
            disabled
          />
        </SStack>
      </SFormGroup>
    </SBox>
  )
}

export default LoginPage
