import React, { Fragment } from "react"

import { IRowsAsideSensorTable, ISelectMenuItemsList } from "@types"
import { DataTable } from "@components"
import { Filter } from "./_Filter"
import { headerTableSensors } from "./_constants"

interface IAsideSensors {
  parameterList: ISelectMenuItemsList
  rows: IRowsAsideSensorTable
  setSelectionRows?: any
  height?: number
}

/**
 * Содержимое вкладки "Сторонние датчики"
 *
 * @param {ISelectMenuItemsList} parameterList - Список параметров для селекта
 * @param {IRowsAsideSensorTable} rows - Содержимое строк
 * @param {    } setSelectionRows - Событие, для получения выбраных строк в таблице
 * @param {number} height - Задает высоту таблицы
 * @returns {React.FC<IAsideSensors>} - FC
 */
export const AsideSensors: React.FC<IAsideSensors> = ({
  parameterList,
  rows,
  setSelectionRows,
  height,
}) => (
  <Fragment>
    <Filter parameterList={parameterList} />
    <DataTable
      rows={rows}
      columns={headerTableSensors}
      setSelectionRows={setSelectionRows}
      height={height}
    />
  </Fragment>
)
