import { Box, Stack, Typography, Button } from "@mui/material"
import React, { ChangeEvent, useState } from "react"

import { BaseTextField, Select } from "@components"
import { ISelectMenuItemsList } from "@types"
import { useDateTimeGenerator } from "@hooks"
import { SCover, SButtonGroup, SSelect, SBaseTextField } from "./_styles"

interface ITimeSync {
  connectedSystemList: ISelectMenuItemsList
  syncMethodList: ISelectMenuItemsList
}

/**
 * Фрагмент "Синхронизация времени"
 *
 * @param {ISelectMenuItemsList} connectedSystemList - Принимает массив значений для селекта "Подключенная система"
 * @param {ISelectMenuItemsList} syncMethodList - Принимает массив значений для селекта "Способ синхронизации"
 * @returns {React.FC<ITimeSync>} - FC
 */
export const TimeSync: React.FC<ITimeSync> = ({
  connectedSystemList,
  syncMethodList,
}) => {
  const [connectedSystem, setConnectedSystem] = useState<string>("Scada 2")
  const [timeSync, setTimeSync] = useState<boolean>(false)
  const [syncMethod, setSyncMethod] = useState<string>("Выберите способ")

  const [date, time] = useDateTimeGenerator()

  return (
    <SCover
      title="Синхронизация времени"
      autoHeight="autoHeight"
      headerChildren={
        <Select
          label="Подключенная система"
          initialState={connectedSystem}
          onChange={(event: ChangeEvent<HTMLInputElement>): void =>
            setConnectedSystem(event.target.value)
          }
          menuItemsList={connectedSystemList}
        />
      }
    >
      <Stack direction="row" alignItems="center" spacing={2.5} mb="30px">
        <Typography variant="body1">Синхронизация времени</Typography>
        <SButtonGroup size="small">
          <Button
            variant={timeSync ? "text" : "contained"}
            onClick={(): void => setTimeSync(!timeSync)}
          >
            Автосинхронизация
          </Button>
          <Button
            variant={timeSync ? "contained" : "text"}
            onClick={(): void => setTimeSync(!timeSync)}
          >
            Ручная установка
          </Button>
        </SButtonGroup>
      </Stack>
      <Stack
        direction="row"
        alignItems="end"
        spacing={6}
        height="130px"
        mb="95px"
      >
        <Box>
          <Stack direction="row" alignItems="center" spacing={7.5} mb="33px">
            <Typography variant="body1">Время системы</Typography>
            <Typography variant="subtitle1">
              {date} {time}
            </Typography>
          </Stack>
          <BaseTextField
            title="Время сервера Echo"
            label="Сервер Echo"
            id="server-time"
            defaultValue={time.toString()}
          />
        </Box>
        {timeSync && (
          <Stack spacing={2.2}>
            <SBaseTextField
              title="Смещение времени"
              label="Время системы"
              id="server-time"
              defaultValue={time.toString()}
            />
            <SSelect
              label="Способ синхронизации"
              initialState={syncMethod}
              size="medium"
              onChange={(event: ChangeEvent<HTMLInputElement>): void =>
                setSyncMethod(event.target.value)
              }
              menuItemsList={syncMethodList}
            />
          </Stack>
        )}
      </Stack>
      <Button size="small" variant="contained">
        Применить
      </Button>
    </SCover>
  )
}
