import {
  FormGroup,
  Box,
  styled,
  Button,
  Stack,
  FormControlLabel,
} from "@mui/material"

const SBox = styled(Box)({
  width: "100%",
  height: "calc(100vh - 100px)",
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
})

const SFormGroup = styled(FormGroup)({
  width: "408px",
})

const SLogo = styled("img")({
  display: "block",
  width: "192px",
  height: "37px",
  marginBottom: "50px",
})

const SButton = styled(Button)({
  width: "164px",
  height: "48px",
})

const SStack = styled(Stack)({
  flexDirection: "row",
  justifyContent: "space-between",
  alignItems: "center",
})

const SFormControlLabel = styled(FormControlLabel)({
  marginRight: 0,
})

export { SBox, SFormGroup, SLogo, SStack, SButton, SFormControlLabel }
