import React, { useState } from "react"

import { IRowsSensorRecordingTable } from "@types"
import { Cover, DataTable, TableEditingButtons } from "@components"
import { Filter } from "./fragments"
import {
  exampleTableEchoRecords,
  headerEchoRecordsTable,
  exampleEquipments,
} from "./_constants"

/**
 * Страница "Записи Echo"
 *
 * @returns {React.FC} - FC
 */
const EchoRecordsPage: React.FC = () => {
  const [selectionRow, setSelectionRow] = useState<IRowsSensorRecordingTable>(
    []
  )

  return (
    <Cover
      title="Записи датчиков"
      footerChildren={
        <TableEditingButtons
          editButtonDisabled
          addingButtonDisabled
          selectionRows={selectionRow}
        />
      }
    >
      <Filter equipmentsList={exampleEquipments} />
      <DataTable
        rows={exampleTableEchoRecords}
        columns={headerEchoRecordsTable}
        setSelectionRows={setSelectionRow}
      />
    </Cover>
  )
}

export default EchoRecordsPage
