import React, { ChangeEvent, useState } from "react"
import { Stack } from "@mui/material"

import { ISelectMenuItemsList } from "@types"
import { SearchForm, Select } from "@components"

interface IFilter {
  parameterList: ISelectMenuItemsList
  equipmentsList: ISelectMenuItemsList
}

/**
 * Фильтр для таблицы "Виртуальные датчики"
 *
 * @param {ISelectMenuItemsList} parameterList - Список параметров для селекта
 * @param {ISelectMenuItemsList} equipmentsList - Список оборудования для селекта
 * @returns {React.FC<IFilter>} - FC
 */
export const Filter: React.FC<IFilter> = ({
  parameterList,
  equipmentsList,
}) => {
  const [parameter, setParameter] = useState<string>("Все")
  const [equipment, setEquipment] = useState<string>("Все")

  return (
    <Stack direction="row" m="10px 15px" spacing={6}>
      <SearchForm />
      <Select
        label="Параметр"
        initialState={parameter}
        menuItemsList={parameterList}
        onChange={(event: ChangeEvent<HTMLInputElement>): void => {
          setParameter(event.target.value)
        }}
      />
      <Select
        label="Оборудование"
        initialState={equipment}
        menuItemsList={equipmentsList}
        onChange={(event: ChangeEvent<HTMLInputElement>): void => {
          setEquipment(event.target.value)
        }}
      />
    </Stack>
  )
}
