import React from "react"

import ChartImg from "@assets/images/graph-for-the-third-party-sensor-page.svg"
import { Cover } from "@components"

/**
 * Франмент графика "Тренд изменения значения во времени".
 * Нет ТЗ по работе графика поэтому добавлено изображение.
 *
 * @returns {React.FC} - FC
 */
export const Chart: React.FC = () => (
  <Cover
    title="Тренд изменения значения во времени"
    autoHeight="autoHeight"
    indent={true}
  >
    <img
      src={ChartImg}
      alt="Charts"
      width="100%"
      height={332}
      style={{ pointerEvents: "none" }}
    />
  </Cover>
)
