import { Stack } from "@mui/material"
import React, { useState } from "react"

import { IRowsModesTable, IRowsTableModeLog } from "@types"
import {
  BasicDateRangePicker,
  Cover,
  DataTable,
  SearchForm,
  TableEditingButtons,
} from "@components"
import { columnsEventTable } from "./_constants"

interface IModeTable {
  rowsTableModeLog: IRowsTableModeLog
  height?: number
}

/**
 * Таблица "Журнал режимов"
 *
 * @param {number} height - Задает высоту таблицы
 * @param {IRowsTableModeLog} rowsTableModeLog - соде
 * @returns {React.FC<IModeTable>} - FC
 */
export const ModeTable: React.FC<IModeTable> = ({
  rowsTableModeLog,
  height,
}) => {
  const [selectionRows, setSelectionRows] = useState<IRowsModesTable>([])

  return (
    <Cover
      title="Журнал режимов"
      autoHeight="autoHeight"
      footerChildren={
        <TableEditingButtons
          selectionRows={selectionRows}
          addingButtonHide
          editButtonHide
        />
      }
    >
      <Stack direction="row" spacing={2} paddingLeft="15px">
        <SearchForm />
        <BasicDateRangePicker />
      </Stack>
      <DataTable
        rows={rowsTableModeLog}
        columns={columnsEventTable}
        height={height}
        setSelectionRows={setSelectionRows}
      />
    </Cover>
  )
}
