type IGetIconStyle = {
  primary: string
  secondary: string
}

/**
 * Функция для получения стилей оформления иконок меню в сайтбаре
 *
 * @param {boolean} isDark - стиль иконок
 * @returns {IGetIconStyle} - объект с цветами орформления иконок меню
 */
export const getIconStyle = (isDark?: boolean): IGetIconStyle =>
  isDark
    ? { primary: "#DB0B5B", secondary: "#FFF" }
    : { primary: "#898989", secondary: "#454443" }
