import { Button, Stack, Typography } from "@mui/material"
import React, { Fragment } from "react"

import ImageScheme from "@assets/images/scheme.svg"
import { BaseTextField, Breadcrumbs, ImageUploadForm, Cover } from "@components"
import { SContainer, SCol, SDescription, SImageBlock } from "./_styles"

/**
 * Страница "Добавление оборудования"
 *
 * @returns {React.FC} - FC
 */
const AddingEquipmentPage: React.FC = () => {
  const breadcrumbs = ["Журнал оборудования", "Добавление нового оборудования"]

  return (
    <Fragment>
      <Breadcrumbs breadcrumbs={breadcrumbs} />
      <Cover
        title="Добавление нового оборудования"
        autoHeight="autoHeight"
        indent={true}
      >
        <Stack direction="row" spacing={6}>
          <BaseTextField title="Название" label="Введите название" id="name" />
          <BaseTextField
            title="Описание"
            label="Введите описание"
            id="description"
          />
        </Stack>
      </Cover>
      <Cover
        title="Установка датчика"
        autoHeight="autoHeight"
        footerChildren={
          <Stack direction="row" spacing={2}>
            <Button variant="contained">Сохранить</Button>
            <Button disabled variant="outlined">
              Отмена
            </Button>
          </Stack>
        }
      >
        <SContainer>
          <SCol>
            <ImageUploadForm />
          </SCol>
          <SCol>
            <Typography variant="body1" children="Расположение устройства" />
            <SDescription
              variant="body2"
              children="Определите устройство на принципиальной схеме, которое хотите добавить"
            />
            <SImageBlock>
              <img
                src={ImageScheme}
                alt="Расположение устройства"
                width="700"
              />
            </SImageBlock>
          </SCol>
        </SContainer>
      </Cover>
    </Fragment>
  )
}

export default AddingEquipmentPage
