import { MenuItem, Typography } from "@mui/material"
import React, { ChangeEvent } from "react"

import { ISelectMenuItemsList } from "@types"
import { SStack, STextField } from "./_styles"

interface ISelectProps {
  label: string
  initialState: string | number
  onChange: (event: ChangeEvent<HTMLInputElement>) => void
  menuItemsList: ISelectMenuItemsList
  size?: "small" | "medium"
}

/**
 * Локальный компонент Select с лейблом
 *
 * @param {string} label - Заголовок селекта
 * @param {string | number} initialState - Значение по умолчанию
 * @param {Function} onChange - Функция для изменение состояния
 * @param {ISelectMenuItemsList} menuItemsList - Список пунктов меню
 * @param {"small" | "medium"} size - Размер селекта
 * @param {object} otherProps - Любые другие значения
 * @returns {React.FC<ISelectProps>} - FC
 */
export const Select: React.FC<ISelectProps> = ({
  label,
  initialState,
  onChange,
  menuItemsList,
  size = "small",
  ...otherProps
}) => (
  <SStack {...otherProps}>
    <Typography variant="body1">{label}</Typography>
    <STextField select value={initialState} onChange={onChange} size={size}>
      {menuItemsList.map(({ value }) => (
        <MenuItem key={value} value={value}>
          {value}
        </MenuItem>
      ))}
    </STextField>
  </SStack>
)
