import { Stack, Typography } from "@mui/material"
import React, { ReactNode, InputHTMLAttributes } from "react"

import { STextField } from "./_styles"

interface ITextField extends InputHTMLAttributes<HTMLInputElement> {
  title: string
  label: string
  variant?: "standard" | "filled" | "outlined"
  id: string
  defaultValue?: string
  childrenStack?: ReactNode
}

/**
 * Локальный компонент TextField с заголовком
 *
 * @param {string} title - Загловок поля ввода (слева)
 * @param {string} label - Загловок над полем ввода
 * @param {"standard" | "filled" | "outlined"} variant - Стиль поля ввода
 * @param {"password" | "text"} type - Тип поля ввода
 * @param {string} id - Уникальный ID, для поля ввода
 * @param {ReactNode} childrenStack - Дополнительное содержимое, для контейнера поля ввода
 * @param {string} defaultValue - Значение поля по умолчанию
 * @param {boolean} disabled - Отключение поля ввода
 * @param {object} otherProps - Любые другие значения
 * @returns {React.FC<ITextField>} - FC
 */
export const BaseTextField: React.FC<ITextField> = ({
  title,
  label,
  variant = "outlined",
  type = "text",
  id,
  childrenStack,
  defaultValue,
  disabled,
  ...otherProps
}) => (
  <Stack direction="row" spacing={3} alignItems="center" {...otherProps}>
    <Typography variant="body1" children={title} />
    <STextField
      id={id}
      label={label}
      variant={variant}
      type={type}
      defaultValue={defaultValue}
      disabled={disabled}
    />
    {childrenStack}
  </Stack>
)
