import React, { Fragment } from "react"

import { Breadcrumbs, ModeTable } from "@components"
import { ModuleDetails, Chart } from "./Fragments"
import { exampleRowsTableModeLog } from "./_constants"

/**
 * Страница отдельного стороннего датчика
 *
 * @returns {React.FC} - FC
 */
const ExternalSensorPage: React.FC = () => {
  const breadcrumbs = ["Журнал датчиков", "Сторонний датчик"]

  return (
    <Fragment>
      <Breadcrumbs breadcrumbs={breadcrumbs} />
      <ModuleDetails title="Температура в котле" />
      <Chart />
      <ModeTable rowsTableModeLog={exampleRowsTableModeLog} height={420} />
    </Fragment>
  )
}

export default ExternalSensorPage
