import DateRangePicker, { DateRange } from "@mui/lab/DateRangePicker"
import DateRangeIcon from "@mui/icons-material/DateRange"
import AdapterDateFns from "@mui/lab/AdapterDateFns"
import LocalizationProvider from "@mui/lab/LocalizationProvider"
import { Button, Typography } from "@mui/material"
import ruLocale from "date-fns/locale/ru"
import React, { Fragment, useState, ReactElement } from "react"

import { SStack, STextField, SButton } from "./_styles"

interface IDatePicker {
  label?: string
  startText?: string
  endText?: string
  size?: "small" | "medium"
}

/**
 * Компонент BasicDateRangePicker
 *
 * @param {string} label - Заголовок дэйтпикера
 * @param {string} startText - Заголовок начальной даты
 * @param {string} endText - Заголовок конечной даты
 * @param {"small" | "medium"} size - Размер поле ввода даты
 * @param {object} otherProps - Любые другие значения
 * @returns {React.FC<IDatePicker>} - FC
 */
export const BasicDateRangePicker: React.FC<IDatePicker> = ({
  label = "Период",
  startText = "Начало",
  endText = "Конец",
  size = "small",
  ...otherProps
}) => {
  const [date, setDate] = useState<DateRange<Date>>([null, null])

  return (
    <LocalizationProvider dateAdapter={AdapterDateFns} locale={ruLocale}>
      <SStack {...otherProps}>
        <Typography variant="body1">{label}</Typography>
        <DateRangePicker
          startText={startText}
          endText={endText}
          value={date}
          components={{ OpenPickerIcon: DateRangeIcon }}
          mask="__.__.____"
          onChange={(newDate: DateRange<Date>): void => setDate(newDate)}
          renderInput={(startProps, endProps): ReactElement => (
            <Fragment>
              <STextField {...startProps} size={size} />
              <STextField {...endProps} size={size} />
            </Fragment>
          )}
        />
        <SButton variant="contained">Применить</SButton>
        <Button variant="outlined" onClick={(): void => setDate([null, null])}>
          Сбросить
        </Button>
      </SStack>
    </LocalizationProvider>
  )
}
