import { Divider, FormControlLabel, Stack, Switch } from "@mui/material"
import React, { ChangeEvent, useState } from "react"

import { ISelectMenuItemsList } from "@types"
import { Cover, Select } from "@components"
import { SStack, STypography, SButton, SSelect } from "./_styles"

interface IGeneralSettings {
  systemLanguagesList: ISelectMenuItemsList
  updateIntervalList: ISelectMenuItemsList
  surveyIntervalList: ISelectMenuItemsList
}

/**
 * Фрагмент "Общие настройки"
 *
 * @param {ISelectMenuItemsList} systemLanguagesList - Принимает массив значений для селекта "Язык системы"
 * @param {ISelectMenuItemsList} updateIntervalList - Принимает массив значений для селекта "Интервал обновлений..."
 * @param {ISelectMenuItemsList} surveyIntervalList - Принимает массив значений для селекта "Интервал опроса..."
 *
 * @returns {React.FC<IGeneralSettings>} - FC
 */
export const GeneralSettings: React.FC<IGeneralSettings> = ({
  systemLanguagesList,
  updateIntervalList,
  surveyIntervalList,
}) => {
  const [language, setLanguage] = useState<string>("Русский")
  const [updateInterval, setUpdateInterval] = useState<number>(30)
  const [surveyInterval, setSurveyInterval] = useState<number>(5)

  return (
    <Cover title="Общие настройки">
      <SStack spacing={2.2}>
        <Select
          label="Язык системы"
          initialState={language}
          onChange={(event: ChangeEvent<HTMLInputElement>): void =>
            setLanguage(event.target.value)
          }
          menuItemsList={systemLanguagesList}
        />
        <Divider />
        <FormControlLabel
          control={<Switch />}
          label="Разрешить изменять настройки"
        />
        <FormControlLabel
          control={<Switch />}
          label="Разрешить подключение новых систем"
        />
        <FormControlLabel
          control={<Switch />}
          label="Отключать приложение в фоновом режиме при сворачивании"
        />
        <Divider />
        <Stack flexDirection="row">
          <STypography variant="body1">
            Место для скачивания аудиозаписей <br />
            <span>D:\Audio</span>
          </STypography>
          <SButton variant="outlined" size="small">
            Изменить
          </SButton>
        </Stack>
        <Divider />
        <SSelect
          label="Интервал обновлений значений датчиков, сек."
          initialState={updateInterval}
          onChange={(event: ChangeEvent<HTMLInputElement>): void =>
            setUpdateInterval(Number(event.target.value))
          }
          menuItemsList={updateIntervalList}
        />
        <SSelect
          label="Интервал опроса доступных систем, мин."
          initialState={surveyInterval}
          onChange={(event: ChangeEvent<HTMLInputElement>): void =>
            setSurveyInterval(Number(event.target.value))
          }
          menuItemsList={surveyIntervalList}
        />
        <Divider />
        <Stack flexDirection="row">
          <STypography variant="body1">
            Место для скачивания аудиозаписей
          </STypography>
          <SButton variant="outlined" size="small">
            Сбросить
          </SButton>
        </Stack>
      </SStack>
    </Cover>
  )
}
