import { useEffect, useState } from "react"

/**
 * Кастомный хук для получения текущей даты и времени
 *
 * @returns {Date[]} - Возвращает массив со значениями датой и времени
 */
export function useDateTimeGenerator(): (string | Date)[] {
  const [date, setDate] = useState<Date | string>(
    new Date().toLocaleDateString()
  )
  const [time, setTime] = useState<Date | string>(
    new Date().toLocaleTimeString()
  )

  useEffect(() => {
    const secTimer = setInterval((): void => {
      setTime(new Date().toLocaleTimeString())
      setDate(new Date().toLocaleDateString())
    }, 1000)

    return () => clearInterval(secTimer)
  }, [])

  return [date, time]
}
