import { Button, Stack, Typography } from "@mui/material"
import React, { ChangeEvent, Fragment, useState } from "react"

import ImageEquipment from "@assets/images/equipment.svg"
import { ISelectMenuItemsList } from "@types"
import { Cover, Select } from "@components"
import { SSensorCanvas, SSensorCanvasTitle } from "./_styles"

interface ISensorInstalling {
  equipmentsList: ISelectMenuItemsList
}

/**
 * Компонент "Добавление нового датчика Echo"
 *
 * @param {ISelectMenuItemsList} equipmentsList - Список оборудования, для селекта
 * @returns {React.FC<ISensorInstalling>} - FC
 */
export const SensorInstalling: React.FC<ISensorInstalling> = ({
  equipmentsList,
}) => {
  const [equipment, setEquipment] = useState<string>("")

  return (
    <Cover
      title="Установка датчика"
      autoHeight="autoHeight"
      indent={true}
      footerChildren={
        <Stack direction="row" spacing={2}>
          <Button variant="outlined" children="Сохранить" disabled />
          <Button
            variant="outlined"
            children="Отмена"
            disabled={!equipment}
            onClick={(): void => setEquipment("")}
          />
        </Stack>
      }
    >
      <Select
        label="Оборудование"
        initialState={equipment}
        menuItemsList={equipmentsList}
        onChange={(event: ChangeEvent<HTMLInputElement>): void => {
          setEquipment(event.target.value)
        }}
      />
      <SSensorCanvas sx={{ borderStyle: !equipment ? "dashed" : "solid" }}>
        {!equipment ? (
          <Typography
            variant="body1"
            children="Выберете оборудование, чтобы оперделить расположении датчика"
          />
        ) : (
          <Fragment>
            <SSensorCanvasTitle children="Определите место на устройстве, где хотите расположить датчик." />
            <img src={ImageEquipment} alt="КТН-1" width="1250" />
          </Fragment>
        )}
      </SSensorCanvas>
    </Cover>
  )
}
