import React, { Fragment } from "react"

import { Breadcrumbs, SensorRecordingTable } from "@components"
import { ModeDescription, AttachedSensors } from "./fragments"

/**
 * Страница "Отдельная запись режима"
 *
 * @returns {React.FC} - FC
 */
const ModePage: React.FC = () => {
  const breadcrumbs = ["Журнал режимов", "Саня что-то включил"]

  return (
    <Fragment>
      <Breadcrumbs breadcrumbs={breadcrumbs} />
      <ModeDescription />
      <AttachedSensors />
      <SensorRecordingTable height={420} />
    </Fragment>
  )
}

export default ModePage
