import React, { Fragment } from "react"

import { Breadcrumbs, ConnectedSensorsTable, ModeTable } from "@components"
import { ModuleDetails } from "./fragments"
import {
  exampleSensorTable,
  exampleAsideSensorTable,
  exampleVirtualSensorTable,
  exampleParameters,
  exampleRecordsStatus,
  exampleConnectionStatus,
  exampleEquipments,
  exampleRowsTableModeLog,
} from "./_constants"

/**
 * Страница "отдельного оборудования"
 *
 * @returns {React.FC} - FC
 */
const EquipmentPage: React.FC = () => {
  const breadcrumbs = ["Журнал оборудований", "Сетевой насос СН-1"]

  return (
    <Fragment>
      <Breadcrumbs breadcrumbs={breadcrumbs} />
      <ModuleDetails
        title="Сетевой насос СН-1"
        parameters={exampleParameters}
      />
      <ModeTable rowsTableModeLog={exampleRowsTableModeLog} height={420} />
      <ConnectedSensorsTable
        echoSensorsRows={exampleSensorTable}
        echoSensorsConnectionStatusList={exampleConnectionStatus}
        echoSensorsRecordsStatusList={exampleRecordsStatus}
        echoSensorsEquipmentsList={exampleEquipments}
        asideSensorsRows={exampleAsideSensorTable}
        asideSensorsParameterList={exampleParameters}
        virtualSensorsRows={exampleVirtualSensorTable}
        virtualSensorsEquipmentList={exampleEquipments}
        virtualSensorsParameterList={exampleParameters}
        autoHeight="autoHeight"
        height={420}
      />
    </Fragment>
  )
}

export default EquipmentPage
