import React, { ChangeEvent, useState } from "react"
import { Stack } from "@mui/material"

import { ISelectMenuItemsList } from "@types"
import { SearchForm, Select } from "@components"

interface IFilter {
  equipmentsList: ISelectMenuItemsList
  connectionStatusList: ISelectMenuItemsList
  recordsStatusList: ISelectMenuItemsList
}

/**
 * Фильтр для таблицы "Сенсоры ECHO"
 *
 * @param {ISelectMenuItemsList} equipmentsList - Список оборудования, для селекта
 * @param {ISelectMenuItemsList} connectionStatusList - Список статусов записи, для селекта
 * @param {ISelectMenuItemsList} recordsStatusList - Список статусов подключения, для селекта
 * @returns {React.FC<IFilter>} - FC
 */
export const Filter: React.FC<IFilter> = ({
  equipmentsList,
  connectionStatusList,
  recordsStatusList,
}) => {
  const [equipment, setEquipment] = useState<string>("Все")
  const [connectStatus, setConnectStatus] = useState<string>("Все")
  const [recordStatus, setRecordStatus] = useState<string>("Все")

  return (
    <Stack direction="row" m="10px 15px" justifyContent="space-between">
      <SearchForm />
      <Select
        label="Оборудование"
        initialState={equipment}
        menuItemsList={equipmentsList}
        onChange={(event: ChangeEvent<HTMLInputElement>): void => {
          setEquipment(event.target.value)
        }}
      />
      <Select
        label="Статус записи"
        initialState={connectStatus}
        menuItemsList={connectionStatusList}
        onChange={(event: ChangeEvent<HTMLInputElement>): void => {
          setConnectStatus(event.target.value)
        }}
      />
      <Select
        label="Статус подключения"
        initialState={recordStatus}
        menuItemsList={recordsStatusList}
        onChange={(event: ChangeEvent<HTMLInputElement>): void => {
          setRecordStatus(event.target.value)
        }}
      />
    </Stack>
  )
}
