import {
  Drawer,
  List,
  ListItemText,
  Stack,
  IconButton,
  styled,
} from "@mui/material"

const SDrawer = styled(Drawer)({
  "width": "141px",
  "flexShrink": 0,

  "& .MuiDrawer-paper": {
    width: "141px",
    boxSizing: "border-box",
    background: "rgba(255, 255, 255, 0.01)",
  },
})

const SList = styled(List)(({ theme }) => ({
  "paddingTop": "20px",

  "a": {
    textDecoration: "none",
  },

  "& .nav_link_active": {
    "background":
      "linear-gradient(90deg, rgba(255, 255, 255, 0.1) -47.13%, rgba(255, 255, 255, 0.022) 200%)",
    "display": "block",

    "& span": {
      color: `${theme.palette.text.primary} !important`,
    },

    "& div": {
      borderRightColor: `${theme.palette.primary.main} !important`,
    },
  },
}))

const SListItemText = styled(ListItemText)(({ theme }) => ({
  marginBottom: 0,

  span: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    textTransform: "uppercase",
    fontWeight: 500,
    fontSize: "14px",
    lineHeight: "24px",
    color: `${theme.palette.grey[700]}`,

    svg: {
      marginBottom: "6px",
    },
  },
}))

const SStack = styled(Stack)(({ theme }) => ({
  "position": "absolute",
  "left": 0,
  "right": 0,
  "bottom": 0,
  "height": "78px",
  "display": "flex",
  "justifyContent": "center",
  "alignItems": "center",
  "flexDirection": "row",
  "borderTopLeftRadius": "39px",
  "backgroundColor": theme.palette.primary.main,
  "& p": {
    fontWeight: "500",
  },
}))

const SIconButton = styled(IconButton)(({ theme }) => ({
  width: "40px",
  height: "40px",
  borderRadius: "6px",
  backgroundColor: theme.palette.primary.light,
}))

export { SDrawer, SList, SListItemText, SStack, SIconButton }
