import { Box, styled } from "@mui/material"

const SContainer = styled(Box)({
  minHeight: "530px",
  maxHeight: "600px",
  display: "flex",
  justifyContent: "space-between",
  overflow: "hidden",
})

const SCol = styled(Box)({
  "borderRight": "1px solid #686868",
  "width": "100%",

  "&:first-child": {
    maxWidth: "320px",
    padding: "27px 20px",
  },
})

const SHeaderCol = styled(Box)({
  borderBottom: "1px solid #686868",
  display: "flex",
  alignItems: "center",
  padding: "0 20px",
  height: "48px",
})

const SBodyCol = styled(Box)({
  display: "flex",
  justifyContent: "center",
  padding: "10px",
  height: "calc(100% - 45px)",

  img: {
    width: "630px",
  },
})

export { SContainer, SCol, SHeaderCol, SBodyCol }
