import { Button, Stack } from "@mui/material"
import React from "react"

import { BaseTextField, Cover } from "@components"
import { STextFieldName, STopStack, SDateRangePicker } from "./_styles"

/**
 * Фрагмент "Описание режима", для страницы "Отдельная запись режима"
 *
 * @returns {React.FC} - FC
 */
export const ModeDescription: React.FC = () => (
  <Cover
    title="Описание режима"
    autoHeight="autoHeight"
    indent={true}
    headerChildren={
      <Stack direction="row" spacing={2}>
        <Button variant="outlined">Редактировать</Button>
        <Button variant="outlined">Удалить</Button>
      </Stack>
    }
  >
    <STopStack direction="row" spacing={8}>
      <STextFieldName
        title="Название"
        label="Введите название"
        id="name"
        defaultValue="Саня че то включил"
      />
      <SDateRangePicker startText="С" endText="До" size="medium" />
    </STopStack>
    <Stack direction="row" spacing={8}>
      <BaseTextField
        title="Оборудование"
        label="Выберите оборудование"
        id="select-equipment"
        defaultValue="80% производительности"
      />
      <BaseTextField
        title="Описание"
        label="Введите описание"
        id="description"
        defaultValue="80% производительности"
      />
    </Stack>
  </Cover>
)
