import { GridColDef } from "@mui/x-data-grid"
import React from "react"
import { Link } from "react-router-dom"

/**
 * Данные для заголовков ячеек таблицы "Сторонние датчики"
 */
const headerTableSensors: GridColDef[] = [
  { field: "id", hide: true, type: "number" },
  {
    field: "time",
    headerName: "Время",
    type: "string",
    flex: 0.001,
  },
  {
    field: "name",
    headerName: "Имя",
    type: "string",
    flex: 0.001,
    renderCell: (params): JSX.Element => (
      <Link to={`/external-sensors/${params.row.id}`}>{params.row.name}</Link>
    ),
  },
  {
    field: "equipment",
    headerName: "Оборудование",
    type: "string",
    flex: 0.001,
  },
  {
    field: "ip_address",
    headerName: "IP адрес",
    type: "string",
    flex: 0.001,
  },
  {
    field: "measured_parameter",
    headerName: "Измеряемый параметр",
    type: "string",
    flex: 0.001,
  },
  {
    field: "current_value",
    headerName: "Текущее значение",
    type: "string",
    flex: 0.001,
  },
]

export { headerTableSensors }
