import React from "react"

import { getIconStyle } from "utils"
import { IIconSvg } from "../types"

/**
 * Иконка Записи Echo. Принимает на вход:
 *
 * @param {string} className - Внешний класс для стилизации
 * @param {string} height - Высота
 * @param {string} width - Ширина
 * @param {string} fill - Цвет
 * @param {boolean} isDark - Стиль иконки темный/яркий
 * @returns {JSX.Element} - JSX
 */
export const IconRecords: React.FC<IIconSvg> = ({
  className = "",
  height = "40",
  width = "40",
  fill = "none",
  isDark,
}) => {
  const iconStyle = getIconStyle(isDark)

  return (
    <svg
      className={className}
      width={width}
      height={height}
      viewBox={`0 0 ${width} ${height}`}
      fill={fill}
    >
      <path
        opacity="0.05"
        d="M32.5 40H7.5C3.3575 40 0 36.6425 0 32.5V7.5C0 3.3575 3.3575 0 7.5 0H32.5C36.6425 0 40 3.3575 40 7.5V32.5C40 36.6425 36.6425 40 32.5 40Z"
        fill="#FFF"
      />
      <path
        d="M10 16.6667V23.3334H14.4444L20 28.8889V11.1111L14.4444 16.6667H10Z"
        fill={iconStyle.secondary}
      />
      <path
        d="M22.2227 16.9366C23.3973 17.4991 24.2068 18.6545 24.2068 20C24.2068 21.3455 23.3973 22.501 22.2227 23.0559V16.9366Z"
        fill={iconStyle.primary}
      />
      <path
        d="M22.2227 14.8993V13.3334C25.4052 14.0251 27.7782 16.7465 27.7782 20C27.7782 23.2536 25.4052 25.975 22.2227 26.6667V25.1008C24.5163 24.447 26.1909 22.4098 26.1909 20C26.1909 17.5903 24.5163 15.5531 22.2227 14.8993Z"
        fill={iconStyle.primary}
      />
    </svg>
  )
}
