import { ButtonGroup, styled } from "@mui/material"

import { BaseTextField, Select } from "@atoms"
import { Cover } from "@components"

const SCover = styled(Cover)({
  padding: "20px",
})

const SButtonGroup = styled(ButtonGroup)(({ theme }) => ({
  "border": `1px solid ${theme.palette.primary.dark}`,

  "& button": {
    padding: "4px 10px",
  },
}))

const SBaseTextField = styled(BaseTextField)({
  "& p": {
    width: "174px",
  },
})

const SSelect = styled(Select)({
  "& .MuiTextField-root": {
    width: "360px",
  },
})

export { SCover, SButtonGroup, SSelect, SBaseTextField }
