import { FormControlLabel, Stack, Switch } from '@mui/material';
import React from 'react';

import { Cover, ImageUploadForm } from '@components';
import { SBox } from './_styles';

/**
 * Фрагмент "Загрузка схемы техпроцесса"
 *
 * @returns {React.FC} - FC
 */
export const SchemaUploader: React.FC = () => (
  <Cover
    title="Загрузка схемы техпроцесса"
  >
    <SBox>
      <ImageUploadForm />
      <Stack m="20px 0 0 10px">
        <FormControlLabel control={<Switch />} label="Разрешить изменение схемы техпроцесса" />
      </Stack>
    </SBox>
  </Cover>
)
