import { Button, Modal, Typography } from "@mui/material"
import React, { ReactNode } from "react"

import { SContent, SHeader, SBody, SFooter } from "./_styles"

interface IBasicModal {
  setIsOpenModal: () => void
  isOpenModal: boolean
  handleButtonSuccess: () => void
  buttonSuccessName?: string
  children: ReactNode
  title: string
}

/**
 * Модальное окно
 *
 * @param {() => void} setOpenModal - Событие открытия модального окна
 * @param {() => void} handleButtonSuccess - Событие для кнопки завершения например: сохранить, отправить и тд
 * @param {boolean} isOpenModal - Открыто окно или нет
 * @param {string} buttonSuccessName - Название кнопки успешного события
 * @param {ReactNode} children - Содержимое модального окна
 * @param {string} title - Заголовок модального окна
 * @returns {React.FC<IBasicModal>} - FC
 */
export const BasicModal: React.FC<IBasicModal> = ({
  setIsOpenModal,
  isOpenModal,
  handleButtonSuccess,
  buttonSuccessName = "сохранить",
  children,
  title,
}) => (
  <Modal open={isOpenModal} onClose={setIsOpenModal}>
    <SContent>
      <SHeader>
        <Typography id="modal-modal-title" variant="h6" component="h2">
          {title}
        </Typography>
      </SHeader>
      <SBody>{children}</SBody>
      <SFooter direction="row" spacing={2}>
        <Button
          disabled
          variant="contained"
          onClick={(): void => {
            handleButtonSuccess()
            setIsOpenModal()
          }}
        >
          {buttonSuccessName}
        </Button>
        <Button variant="outlined" onClick={setIsOpenModal}>
          Отмена
        </Button>
      </SFooter>
    </SContent>
  </Modal>
)
