import React, { Fragment } from "react"

import { Breadcrumbs, ModeTable } from "@components"
import { ModuleDetails, SensorParameters } from "./Fragments"
import {
  rowsTableSensorParameters,
  exampleRowsTableModeLog,
} from "./_constants"

/**
 * Страница оттдельного виртульного датчика
 *
 * @returns {React.FC} - FC
 */
const VirtualSensorPage: React.FC = () => {
  const breadcrumbs = ["Журнал датчиков", "Название виртуального датчика"]

  return (
    <Fragment>
      <Breadcrumbs breadcrumbs={breadcrumbs} />
      <ModuleDetails title="Температура в котле" />
      <SensorParameters rowsSensorParameters={rowsTableSensorParameters} />
      <ModeTable rowsTableModeLog={exampleRowsTableModeLog} height={420} />
    </Fragment>
  )
}

export default VirtualSensorPage
