import { Button, Stack, TextField, Typography } from "@mui/material"
import React from "react"

import ImageScheme from "@assets/images/scheme.svg"
import ImageEquipment from "@assets/images/equipment.svg"
import { Cover } from "@components"
import {
  SContainer,
  SCol,
  SHeaderCol,
  SBodyCol,
  SConnectionStatus,
} from "./_styles"

interface IModuleDetails {
  title: string
}

/**
 * Фрагмент с детальной инфомацией о модуле
 *
 * @returns {React.FC} - FC
 */
export const ModuleDetails: React.FC<IModuleDetails> = ({ title }) => (
  <Cover title={title} autoHeight="autoHeight">
    <SContainer>
      <SCol>
        <SConnectionStatus status={true} />
        <Stack direction="column" spacing={3}>
          <TextField
            id="outlined-basic"
            label="Измеряемый параметр"
            variant="outlined"
            defaultValue="Температура"
            fullWidth
          />
          <TextField
            id="outlined-basic"
            label="Значение датчика"
            variant="outlined"
            defaultValue="389°C"
            fullWidth
          />
          <TextField
            id="outlined-multiline-static"
            label="Описание"
            multiline
            rows={4}
            defaultValue="Тorem ipsum dolor sit amet, consectetur adipiscing elit. Ullamcorper orci luctus aliquet risus venenatis id enim eget."
            size="small"
            fullWidth
          />
        </Stack>
      </SCol>
      <SCol>
        <SHeaderCol
          direction="row"
          alignItems="center"
          justifyContent="space-between"
        >
          <Typography variant="body1">
            Расположение на принципиальной схеме
          </Typography>
          <Button variant="outlined">Изменить</Button>
        </SHeaderCol>
        <SBodyCol>
          <img src={ImageScheme} alt="Принципиальная схема" width="630" />
        </SBodyCol>
      </SCol>
      <SCol>
        <SHeaderCol
          direction="row"
          alignItems="center"
          justifyContent="space-between"
        >
          <Typography variant="body1">КТН-1</Typography>
          <Button variant="outlined">Изменить</Button>
        </SHeaderCol>
        <SBodyCol>
          <img src={ImageEquipment} alt="КТН-1" width="630" />
        </SBodyCol>
      </SCol>
    </SContainer>
  </Cover>
)
