import { Stack } from '@mui/material';
import React, { Fragment } from 'react';

import { GeneralSettings, SchemaUploader, TimeSync } from './fragments';
import {
  exampleConnectedSystem,
  exampleSurveyInterval,
  exampleSystemLanguages,
  exampleUpdateInterval,
  exampleSyncMethod
} from './_constants';

/**
 * Страница "Настройки"
 *
 * @returns {React.FC} - FC
 */
const SettingsPage: React.FC = () => (
  <Fragment>
    <Stack direction="row" spacing={2.5} mb="20px">
      <GeneralSettings
        systemLanguagesList={exampleSystemLanguages}
        updateIntervalList={exampleUpdateInterval}
        surveyIntervalList={exampleSurveyInterval}
      />
      <SchemaUploader/>
    </Stack>
    <TimeSync
      connectedSystemList={exampleConnectedSystem}
      syncMethodList={exampleSyncMethod}
    />
  </Fragment>
)

export default SettingsPage
