import { Button, Stack } from "@mui/material"
import React, { Fragment } from "react"

import { Breadcrumbs } from "@components"
import { AttachedSensors, ModeDescription } from "./fragments"

/**
 * Страница "Добавление нового режима"
 *
 * @returns {React.FC} - FC
 */
const AddingModePage: React.FC = () => {
  const breadcrumbs = ["Журнал режимов", "Новая запись"]

  return (
    <Fragment>
      <Breadcrumbs breadcrumbs={breadcrumbs} />
      <ModeDescription />
      <AttachedSensors />
      <Stack direction="row" spacing={2} justifyContent="flex-end">
        <Button variant="contained" size="small">
          Сохранить
        </Button>
        <Button variant="outlined" size="small" disabled>
          Отмена
        </Button>
      </Stack>
    </Fragment>
  )
}

export default AddingModePage
