import React, { useState } from "react"

import { IRowsModesTable } from "@types"
import { Cover, DataTable, TableEditingButtons } from "@components"
import { Filter } from "./fragments"
import {
  exampleModesTable,
  headerTableModes,
  exampleEquipment,
} from "./_constants"

/**
 * Страница "Режимы"
 *
 * @returns {React.FC} - FC
 */
const ModesPage: React.FC = () => {
  const [selectionRow, setSelectionRow] = useState<IRowsModesTable>([])

  return (
    <Cover
      title="Режимы"
      footerChildren={
        <TableEditingButtons
          selectionRows={selectionRow}
          addingButtonPatch="/modes/adding"
        />
      }
    >
      <Filter equipmentsList={exampleEquipment} />
      <DataTable
        rows={exampleModesTable}
        columns={headerTableModes}
        setSelectionRows={setSelectionRow}
      />
    </Cover>
  )
}

export default ModesPage
